<template>
  <div id="app">
    <!-- <img src="./assets/logo.png"> -->
    <!-- <TodoList /> -->
    <!-- <api /> -->
   <!--  <hello-world /> -->
  <!--  <Myanimate :progress="progress">
    </Myanimate> -->
    <el-row>
      <el-col :span="24"> 
        <el-menu 
        :default-active="activeIndex" 
        class="el-menu-demo" 
        :router="true" 
        mode="horizontal" 
        @select="handleSelect">
          <el-menu-item index="/">首页</el-menu-item>
          <el-menu-item index="/list">TodoList</el-menu-item>
          <el-menu-item index="/douban" >豆瓣电影TOP250</el-menu-item>
          <el-menu-item index="/3" disabled>暂未开放</el-menu-item>
        </el-menu>
      </el-col>
    </el-row>
    <el-row>
       <el-col :span="24">
         <router-view></router-view>
       </el-col>
    </el-row>
   <!--  <div class="loading" v-if="progress<100">
      <Myanimate :progress="progress" class="vcenter"></Myanimate> 
    </div> -->
  </div>
</template>
<script>
    import HelloWorld from "./components/HelloWorld";
    import TodoList from "./components/TodoList.vue";
    import api from "./components/api.vue";
    import Myanimate from "./components/animate";
    import index from "./components/myIndex";
    import "element-ui/lib/theme-chalk/index.css";
    export default {
    name: "App",
    components: {
        HelloWorld,
        TodoList,
        api,
        Myanimate,
        index
    },
    mounted() {
        this.changeProgress();
    },
    data() {
        return {
        progress: 0,
        activeIndex: "/"
        };
    },
    updated(){
        /* let href=location.href.split("").slice(location.href.indexOf("#")+1).join("");
        this.activeIndex=href; */
    },
    methods: {
        changeProgress() {
        let timer = setInterval(() => {
            this.progress = parseInt(this.progress) + 2;
            if (this.progress >= 100) {
            clearInterval(timer);
            }
        }, 60);
        },
        handleSelect(key, keyPath) {
        console.log(key, keyPath);
        }
    }
    };
</script>

<style lang="scss">
    #app {
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        color: #2c3e50;
        width: 100%;
        padding: 0;
        margin: 0;
    }
    .nav {
        text-decoration: none;
    }
    .loading{
        width: 100%;
        height: 100%;
        position: fixed;
        left: 0;
        top: 0;
        background: rgba($color: #ddd, $alpha: 0.6);
        padding: auto;
        .vcenter{
            left: 0;
            top: 0;
            right: 0;
            bottom: 0;
            position: absolute;
            margin: auto;
        }
    }
</style>



