<template>
	<div>
		<el-row>
			<h2>TodoList</h2>
			<div class="color-grid warning-row">已过期</div><div class="color-grid success-row">即将到来</div>
		</el-row>
		<el-row>
			<el-col>
				<TodoForm :sonTodo="Todo" @sendTodo="getTodo" isAdd inline>
				</TodoForm>
			</el-col>
		</el-row>
		 <el-table
      		:data="Todos"
      		style="width: 100%"
			height="400"
			v-show="Todos.length>0"
			v-loading="loading"
			:row-class-name="tableRowClassName">
			<el-table-column
				type="index"
				width="80"
				fixed>
			</el-table-column>
			<el-table-column
				prop="title"
				label="标题"
				width="180">
			</el-table-column>
			<el-table-column
				prop="describe"
				label="描述"
				width="180">
			</el-table-column>
			<el-table-column
				prop="date"
				label="日期">
			</el-table-column>
			<el-table-column
				prop="time"
				label="时间">
			</el-table-column>
			<el-table-column
				label="操作">
				<template slot-scope="scope">
					<el-button
						@click="removeTodo(scope.$index)"
						type="text"
						size="small"
						class="Todo-button">
						移除
					</el-button>
					<el-button 
						type="text" 
						@click="edit(scope.$index)"
						size="small"
						class="Todo-button">
						编辑
						</el-button>
				</template>
			</el-table-column>
    	</el-table>
		<el-dialog title="编辑" :visible.sync="dialogFormVisible">
			<todo-form :sonTodo="myTodo" @sendTodo="getTodo" :inline="false" ref="_TodoForm"></todo-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="dialogFormVisible = false">取 消</el-button>
				<el-button type="primary" @click="changeTodo">更 改</el-button>
			</div>
		</el-dialog>
	</div>
</template>

<script>
	import TodoForm from "./TodoForm.vue"
	export default{
		name:"TodoList",
		created(){
			let Todos=JSON.parse(localStorage.getItem("myTodos"));
			if(Todos){
				this.Todos=Todos;
			}
			//this.Todos=JSON.parse(localStorage.getItem("myTodos"));
		},
		components:{
			TodoForm
		},
		updated(){
			//this.timestamp=Date.parse(new Date());
		},
		mounted(){
			this.loading=false;
		},
		data(){
			return{
				Todos:[],
				Todo:{
					title:"",
					describe:"",
					date:"",
					time:"",
					timestamp:-1
				},
				items:[],
				num:0,
				loading:true,
				timestamp:Date.parse(new Date()),
				dialogTableVisible: false,
				dialogFormVisible: false,
				myTodo:{},
				index:0
			}
		},
		methods:{
			randomIndex: function () {
		      return Math.floor(Math.random() * this.items.length)
		    },
			tableRowClassName({row, rowIndex}) {
				if(this.Todos[rowIndex].timestamp<this.timestamp){
					return "warning-row"
				}else if(this.Todos[rowIndex].timestamp-86400000<this.timestamp){
					return "success-row"
				}
				return "";
			},
			getTodo(Todo){
				let _Todo={
					title:Todo.title,
					describe:Todo.describe,
					date:Todo.date,
					time:Todo.time,
					timestamp:Todo.timestamp
				}
				if(!this.dialogFormVisible){
					this.Todos.push(_Todo);
				}else{
					console.log(this.index,_Todo)
					this.Todos.splice(this.index,1,_Todo);
				}
				this.Todo={title:"",describe:"",time:"",date:"",timestamp:-1};
				localStorage.setItem("myTodos", JSON.stringify(this.Todos));
				this.dialogFormVisible = false;
			},
			removeTodo(index){
				this.Todos.splice(index,1);
				localStorage.setItem("myTodos", JSON.stringify(this.Todos));
			},
			edit(index){
				this.index=index;
				this.myTodo=JSON.parse(JSON.stringify(this.Todos[index]));
				this.dialogFormVisible = true;
			},
			changeTodo(){
				this.$refs._TodoForm.addsonTodo();
			}
		}
	}
</script>
<style>
	*{
		font-family: "微软雅黑","黑体","楷体";
	}
	.fade-enter-active, .fade-leave-active {
		transition: all .5s;
	}
	.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
		opacity: 0;
		transform: scale(2);
	}
	.warning-row,.el-table .warning-row {
		background: rgb(250, 223, 206);
	}
	.success-row,.el-table .success-row {
		background: rgb(207, 252, 211);
	}
	.color-grid{
		display: inline-block;
		width: 100px;
		height: 30px;
		line-height: 30px;
		text-align: center;
		font-size: 16px;
		color: #666;
		margin: 0 5px 5px 0;
	}
	.Todo-button{
		display: inline-block;
		margin: 0;
		padding: 0 2px;
	}
	.el-table .cell .el-button+.el-button{
		margin: 0;
	}
	.el-dialog{
		min-width: 320px;
	}
	@media screen and (max-width: 580px) {
		.el-dialog{
			width: 100%;
			overflow: hidden;
		}
	}
	
</style>


