<template>
	<div class="Test">
		<P :class='active'>{{msg}}-{{title}}-{{title2.value}}</P>
		<ul>
	  		<li v-for="item in items" @click='getInfo'>{{item}}</li>
	  	</ul>
	 	 	<input type="text" @keydown.enter='getKey' v-model='input'>
	 	 	<button @click="sendInfo">发送信息</button>
			<slot name="s2" text="传递信息">默认信息</slot>
			<slot></slot>
	</div>
</template>
<script>
	export default {
	  name: 'Test',
	  data () {
	    return {
	      msg: '你好',
	      items:[1,2,3,4,5,6,7],
	      input:'',
	      active:'active'
	    }
	  },
	  methods:{
	  	getInfo(e){
	  		e.target.setAttribute('class','active');
	  	},
	  	getKey(){
	  		this.items.push(this.input);
	  		this.input='';
	  	},
	  	sendInfo(){
	  		this.$emit('sendInfo',this.msg);
	  	}
	  },
	  watch:{
	  	items:function(data){
	  		//console.log(data)
	  		console.log(1)
	  		this.msg='enter'
	  	}
	  },
	  computed:{
	  	
	  },
	  props:{
	  	title:{
	  		type:String,
	  		required :true,

	  	},
	  	title2:{
	  		type:Object,
	  		default:function(){
	  			return {name:' ',value:0}
	  		},
	  		validator:function(obj){
	  			return obj.value>9
	  		}
	  	}
	  }
	}
</script>
<style>
ul{
	list-style:none;
}
li{
	float: left;
}
.active{
	background: #fff
}
</style>


