<template>
  <div class="hello">
    <input type="text"  v-model='title'>
    <Test :title="title" :title2="title2" @sendInfo="getInfo">
         <div>匿名</div>
        <div slot="s2" slot-scope="props">
            {{props.text}}
        </div> 
    </Test>
  </div>
</template>

<script>
import Test from "./Test"
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Hello Vue',
      title:'Hello Vue',
      title2:{
        name:'hello',
        value:10,
      }
    }
  },
  components:{
      Test
  },
  methods:{
    getInfo(data){
      console.log(data)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



