<template>
    <div class="movie">
        <el-row class="movie-info-container">
            <el-col :xs="8" :sm="8" :lg="{span: 6, offset: 2}">
                <img :src="movie.images.large" class="movie-cover">
            </el-col>
            <el-col :xs="16" :sm="16" :lg="14" class="movie-info">
                <h4><slot name="rank"></slot>{{movie.title}}{{movie.original_title?"/"+movie.original_title:""}}</h4>
                <p>导演: {{getDirectots}}</p>
                <p>主演: {{getCasts}}</p>
                <p>上映时间: {{movie.year}}</p>
                <p>标签: 
                    <span v-for="tag in movie.genres" class="movie-tags">
                        <el-tag size="medium">{{tag}}</el-tag>
                    </span>
                </p>
                <p>评分:<span class="movie-star">
                    <el-rate
                            v-model="getStars"
                            manx="10"
                            disabled
                            show-score
                            :colors="['#99A9BF', '#F7BA2A', '#FF9900']"
                            >
                        </el-rate>
                </span>
                </p>
            </el-col>
        </el-row>
    </div>
</template>
<script>
export default {
    name:"DoubanMovie",
    data(){
        return{
            
        }
    },
    computed:{
        getDirectots(){
            return getNames(this.movie.directors);
        },
        getCasts(){
            return getNames(this.movie.casts);
        },
        getStars(){
            return parseFloat(this.movie.rating.average)
        }
    },
    props:{
        movie:{
            type:Object
        }
    },
    mounted(){
        /* console.log(this.movie) */
    }
}
function getNames(peos){
    if(peos.length==1){
        return peos[0].name;
    }else if(peos.length>1){
        let strs="";
        for(let i=0,j=peos.length-1;i<j;i++){
            strs+=peos[i].name+"/";
        }
        strs+=peos[peos.length-1].name;
        return strs;
    }
    return "暂未收录";
}
</script>
<style scoped>
    .movie{
        margin-bottom: 20px;
        padding-bottom: 20px;
        border-bottom: 1px solid #888;
    }
    .movie-info{
        text-indent: 2px;
        padding: 0;
        overflow: auto;
        letter-spacing: 2px;
        font-size: 20px;
    } 
    .movie-info>h4{
        margin-bottom: 50px;
        font-size: 22px;
    }
    .movie-info>*{
        /* text-overflow: ellipsis; */
        white-space: nowrap;
    } 
    .movie-info .movie-star{
        display:inline-block;
        position: relative;
        bottom: 3px;
    }
    .movie-info>p:last-child{
        margin: 0;
    }
    .movie-info .movie-tags{
        padding: 0;
        margin: 0;
        margin-right: 8px;
    }
    .movie-info>*{
        margin: 0 0 20px 0;
    }
    ::-webkit-scrollbar {/*滚动条整体样式*/
          /*高宽分别对应横竖滚动条的尺寸*/
        height: 5px;
    }
    ::-webkit-scrollbar-thumb {/*滚动条里面小方块*/
        border-radius: 10px;
        box-shadow: inset 0 0 5px rgba(0,0,0,0.2);
        background: #cfcfcf;
    }
    ::-webkit-scrollbar-track {/*滚动条里面轨道*/
        border-radius: 10px;
        background: rgba(0,0,0,0)
    }
    .movie-cover{
        width: 200px;
    }
    @media screen and (max-width: 768px){
        .movie-info>h4{
            margin-bottom: 20px;
            font-size: 18px;
        }
        .movie-info>*{
            margin: 0 0 20px 0;
            font-size: 16px;
        }
        .movie-cover{
            width: 160px;
        }
        .movie-info{
            padding: 0 0 0 2px;
        }
        .movie-info .movie-tags{
            margin-right: 4px;
        }
    }
    @media screen and (max-width: 500px){
        .movie-cover{
            width: 135px;
        }
        .movie-info>h4{
            margin-bottom: 10px;
        }
        ::-webkit-scrollbar {
            height: 4px;
        }
        .movie-info>*{
            margin: 0 0 14px 12px;
        }
        .movie-info{
            margin: 0 -20px 0 20px;
        }
        .movie-info>p:last-child{
            margin-left: 12px;
        }
        .movie-info .movie-tags>span{
            height: 18px;
            line-height: 16px;
        }
    }
    @media screen and (max-width: 374px){
        .movie-cover{
            width: 130px;
        }
        ::-webkit-scrollbar {
            height: 2px;
        }
        .movie-info{
            margin: 0 0 20px 0;
        }
        .movie-info>*{
            margin: 0 0 2px 8px;
        }
        .movie-info>p:last-child{
            margin-left: 8px;
        }
        .movie-cover{
            width: 100%;
        }
    }
</style>



