<template>
    <div>
        <el-row>
            <el-col :span="24">
                <div class="top-info">
                    <h2>豆瓣电影TOP250</h2>
                </div>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="24">
                <ul class="movie-list">
                    <li v-for="(movie,index) in movies">
                        <DoubanMovie :movie="movie">
                            <span slot="rank" :class="index<3?['movie-rank'+index,'movie-rank']:'movie-rank'">{{index+1}}</span>
                        </DoubanMovie>
                    </li>
                    <div id="loading" v-show="isloading">
                        <div class="el-loading-spinner"><svg viewBox="25 25 50 50" class="circular"><circle cx="50" cy="50" r="20" fill="none" class="path"></circle></svg><!----></div>
                    </div>
                </ul>
            </el-col>
        </el-row>
    </div>
</template>
<script>
import DoubanMovie from "./DoubanMovie";
const jsonp = require('jsonp');
export default {
    name:"Douban",
    data(){
        return{
            movies:[],
            len:0,
            isloading:true
        }
    },
    components:{
        DoubanMovie
    },
    methods:{
        getinfo(start){
            this.isloading=true;
            jsonp('http://query.yahooapis.com/v1/public/yql?q='+
            `select * from json where url=\"api.douban.com/v2/movie/top250?start=${start}\"&format=json`
            , null, (err, data) => {
                if (err) {
                    console.error(err.message);
                    this.isloading=false;
                } else {
                    console.log(data)
                    let result=JSON.parse(JSON.stringify(data)).query.results["json"];
                    console.log(result);
                    this.movies.length>0?this.movies=this.movies.concat(result.subjects):this.movies=result.subjects;
                    localStorage.setItem("movies",JSON.stringify(this.movies));
                    this.isloading=false;
                    this.len>=240?this.len=250:this.len+=20;
                }
            });
           /*  this.$ajax({    
                    url: 'http://query.yahooapis.com/v1/public/yql',    
                    dataType: 'jsonp',    
                    data: {    
                        q: `select * from json where url=\"api.douban.com/v2/movie/top250\"`,   
                        format: "json"    
                    },    
                    success: function (data) { 
                        
                    },
                    error:function(error){
                        alert("雅虎抽风了 请稍后再试");
                    }
            }).then(function(data){
                let result=JSON.parse(JSON.stringify(data)).query.results["json"];
                console.log(result);
                this.movies=result.subjects;
                localStorage.setItem("movies",this.movies);
            }).catch(function(err){
                 alert("雅虎抽风了 请稍后再试");
            }); */
        },
        changeScroll(){
            //滚动距离
            let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
            //页面高度
            let height =  document.documentElement.clientHeight;
            //总高度
            let scrollHeight = document.body.scrollHeight ;
            if(scrollTop+height+20>=scrollHeight&&!this.isloading){
                this.isloading=true;
                console.log("你好")
                this.getinfo(this.len);
            }
        }
    },
    mounted(){
        window.addEventListener('scroll', this.changeScroll);
    },
    destroyed () {
        window.removeEventListener('scroll', this.changeScroll)
    },
    created(){
        if(localStorage.getItem("movies")){
            //this.movies=localStorage.getItem("movies");
            this.movies=JSON.parse(localStorage.getItem("movies"));
            this.len=this.movies.length;
            console.log(this.movies)
            this.isloading=false;
        }else{
            console.log("初始化");
            this.getinfo(this.len);
        }
    }
}
</script>
<style scoped>
    .top-info{
        background: rgb(160, 186, 235);
        color: white;
        width: 100%;
        height: 50px;
        line-height: 50px;
        text-align: center
    }
    .movie-list{
        list-style: none;
        width: 100%;
        padding: 0;
    }
    .movie-list>li{
        width: 100%;
    }
    .movie-rank{
        display: inline-block;
        width: 3rem; 
        font-size: 20px;
        color: white;
        text-align: center;
        margin-right: 2px;
        background: rgb(134, 169, 243);
    }
    .movie-rank0{
        background: red;
    }
    .movie-rank1{
        background: rgb(250, 103, 35);
    }
    .movie-rank2{
        background: rgb(255, 209, 81);
    }
    #loading{
        height: 55px;
        position: relative;
    }
    @media screen and (max-width: 768px){
        .movie-rank{
            width: 2.8rem; 
            font-size: 16px;
        }
    }
</style>



