<template>
    <div class="container">
        <span class="progress" :style="{color:myFontColor}">{{progress?progress:0}}%</span>
        <div class="water" :style="{background:bkground}">
            <div class="wave" :style="{top:myProgress}"></div>
        </div>
    </div>
</template>
<script>
export default {
  name:'Myanimate',
  data(){
      return{
          myFontColor:"",
      }
  },
  mounted(){
      this.myFontColor=this.fontColor;
  },
  props: {
      progress:{},
      fontColor:{
        default: function () {
            return "blue"
        }
      },
      bkground:{
        default: function () {
            return "rgb(118, 218, 255)"
        }
      }
  },
  computed:{
      myProgress(){
          let i=parseInt(this.progress);
          if(i>=55)this.myFontColor="white"
          return  -i+35+"%"
      }
  }
}
</script>
<style lang="scss" scoped>
    @mixin box ($width:100px,$height:100px){
        width: $width;
        height:$height;
    }
    @mixin content($width:90px,$height:90px){
        width: $width;
        height:$height;
        left:calc(50% - #{$width / 2});
        top:calc(50% - #{$height / 2})
    }
    .container{
        border-radius:50%;
        transform: scale(2);
        //margin: 50px;
        @include box;
        overflow: hidden;
        border:3px solid rgb(90, 154, 250);
        box-shadow: 3px 1px 1px rgba(0,0,0,0.1);
        .progress{
            position: absolute;
            display: inline-block;
            margin: auto;
            bottom:0;
            top: 0;
            left: 0;
            right: 0;
            z-index: 30;
            text-align: center;
            line-height:100px;
        }
        .water{
            position: relative;
            @include content;
            background-color: rgb(118, 218, 255);
            box-sizing: border-box;
            border-radius: 50%;
            overflow: hidden;
            border: 5px solid rgba(255,255,255,0.85);
            box-shadow: 3px 1px 2px rgba(98, 133, 228, 0.384);
            .wave{
                position: absolute;
                width: 200%;
                height: 200%;
                top:15%;
                left:50%;
                background-color: rgba(255,255,255,0.7);
                border-radius: 45%;
                transform: translate(-50%, -70%) rotate(0);
                animation: rotate 3s linear infinite;
                z-index: 10;
                &::after{
                    content:'';
                    border-radius: 42%;
                    position: absolute;
                    display: block;
                    top:0;
                    left:0;
                    right: 0;
                    bottom: 0;
                    width: calc(100% + 2px);
                    height:calc(100% + 2px);
                    background-color: rgba(255,255,255,0.5);
                    transform:rotate(10deg);

                    z-index: 20;
                }
            }
        }
    }
    @keyframes rotate {
        
        100% {
            transform: translate(-50%, -70%) rotate(350deg);
        }
    }
</style>




