<template>
	<div>
		<input type="text" v-model="title[0].title">
		<button @click="sendInfo">子组件传递给父组件消息</button>
		<ul v-for="t in title">
			<li>{{t.title}}</li>
			<li>{{t.content}}</li>
		</ul>
	</div>
</template>
<script>
	export default{
		name:"myslot",
		data(){
			return{
				test:'aa'
			}
		},
		props:{
			title:{
				type:Array,
				require:true,
				validator:function(value){
					return true;
				}

			}
		},
		methods:{
			sendInfo(){
				this.$emit("test",this.test);
			}
		}
	}
</script>
<style>
	
</style>


