<template>
	<div >
		api
		<myslot :title="titile" @test="getinfo">
		</myslot>
	</div>
</template>
<script>
	import myslot from "./myslot.vue"
	export default{
		name:'api',
		data(){
			return{
				titile:[
					{"title":"111","content":"content1"},
					{"title":"123","content":"content2"}
				]
			}
		},
		components:{
			myslot
		},
		methods:{
			getinfo(value){
				console.log("我是子组件的prop:"+value);
			}
		}
	}
</script>
<style>
	
</style>


