<template>
    <div>
        <el-form ref="form" :model="sonTodo" label-width="80px" class="demo-form-inline" :inline="inline">
            <el-form-item label="标题：">
                <el-input v-model="sonTodo.title" type="text"></el-input>
            </el-form-item>
            <el-form-item label="描述：">
                <el-input v-model="sonTodo.describe" type="text"></el-input>
            </el-form-item>
            <el-form-item label="日期：">
                <el-date-picker
                    v-model="sonTodo.date"
                    type="date"
                    placeholder="选择日期"
                    value-format="yyyy-MM-dd"
                    :picker-options="pickerOptions">
                </el-date-picker>
            </el-form-item>
            <el-form-item label="时间：">
                <el-time-picker
                    v-model="sonTodo.time"
                    :picker-options="timeOptions"
                    placeholder="选择时间点"
                    value-format="HH:mm:ss">
                </el-time-picker>
            </el-form-item>
            <el-form-item>
                <el-button @click="addsonTodo" v-if="isAdd">添加</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
import {dateFormat} from './myToolkits/dateFormat.js'
export default {
    name:"sonTodoForm",
    data(){
        return{
            pickerOptions: {
                disabledDate(time) {
                    return time.getTime()+ 3600 * 1000 * 24 <= Date.now();
                },
                shortcuts: [{
                    text: '今天',
                    onClick(picker) {
                        picker.$emit('pick', new Date());
                    }
                }, {
                    text: '明天',
                    onClick(picker) {
                        const date = new Date();
                        date.setTime(date.getTime() + 3600 * 1000 * 24);
                        picker.$emit('pick', date);
                    }
                }, {
                text: '一周后',
                onClick(picker) {
                    const date = new Date();
                    date.setTime(date.getTime() + 3600 * 1000 * 24 * 7);
                    picker.$emit('pick', date);
                    }
                }]},
            timeOptions:{
                /* dateFormat(new Date(),'hh:mm:ss')+ */
                selectableRange: dateFormat(new Date(),'hh:mm:ss')+' - 23:59:59',
            },
        }
    },
    methods:{
        addsonTodo(){
            for(let t in this.sonTodo){
                if(this.sonTodo[t]==""){
                    console.log("不能为空",this.sonTodo[t]);
                    this.$notify({
                        title: '警告',
                        message: '请输入内容',
                        type: 'warning'
                    });
                    return ;
                }
            }
            let date = new Date(this.sonTodo.date+' '+this.sonTodo.time);
            this.sonTodo.timestamp=Date.parse(date);
            this.$emit('sendTodo',JSON.parse(JSON.stringify(this.sonTodo)));
            this.$notify({
                title: '成功',
                message: '已为您记录',
                type: 'success'
            });
        }
    },
    props:{
        sonTodo:{
            type:Object
        },
        isAdd:{
            type:Boolean
        },
        inline:{
            type:Boolean
        }
    },
}
</script>

<style scoped>

</style>



